import React, { Component } from 'react'
import HeaderComponent from './../Partials/headerComponent';
import SidebarComponent from './../Partials/sidebarComponent';
import { connect } from 'react-redux';
import { fetchProducts } from './../../../actions/productActions';
import { Alert, Table } from 'reactstrap';

class manageProducts extends Component {

    constructor(props) {
        super(props);

        this.state = {
            infoMessage: true,
            productsArray: []
        }
    }

    componentDidUpdate(prevProps) {

        const { products } = this.props;

        if (products !== prevProps.products) {
            this.setState({ productsArray: products.data })
        }

    }

    onClickFunction = () => {
        this.setState({ infoMessage: false })
    }

    componentDidMount() {
        this.props.fetchProducts();
    }

    render() {

        const { productsArray } = this.state;

        const renderProducts = productsArray.map((product, index) => {
            return <div key={index}>
                <Table bordered >
                    <thead>
                        <tr>
                            <th>Principal</th>
                            <th>FBA Fees</th>
                            <th>Referral Fees</th>
                            <th>Amazon Fees</th>
                            <th>Quantity Available</th>
                            <th>Product Posted</th>
                            <th>Seller SKU</th>
                            <th>ASIN</th>
                            <th>Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>$ {product.Principal}</td>
                            <td>$ {product.FBA}</td>
                            <td>$ {product.ReferralFee}</td>
                            <td>$ {product.amznFeePerOrder}</td>
                            <td>{product.AvailableQuantity}</td>
                            <td>{product.PostedDate}</td>
                            <td>{product.SellerSKU}</td>
                            <td>{product.ASIN}</td>
                            <td>{product.ProductDesc}</td>
                        </tr>
                    </tbody>
                </Table>

                <hr />

            </div>
        })

        return (
            <div>
                <div className="main-panel">
                    <SidebarComponent propData={this.props} />
                    <div className="page-container">
                        {
                            this.state.infoMessage ? (
                                <div onClick={this.onClickFunction}>
                                    <Alert color="warning">It might take some time to synchronize all your products.</Alert>
                                </div>
                            ) : null
                        }
                        <HeaderComponent propData={this.props} />

                        <div className="inner-panel">

                            <h4><u>My Products</u></h4>

                            <br />

                            <Table bordered responsive >
                                <thead>
                                    <tr>
                                        <th>Principal</th>
                                        <th>FBA Fees</th>
                                        <th>Referral Fees</th>
                                        <th>Product Added</th>
                                        <th>Seller SKU</th>
                                        <th>ASIN</th>
                                        <th>Description</th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {
                                        productsArray.map((product, index) => {
                                            return <tr>
                                                {
                                                    product.Principal ? (<td>$ {product.Principal}</td>) :
                                                        <td>N/A</td>
                                                }
                                                {
                                                    product.FBA ? (<td>$ {product.FBA}</td>) :
                                                        <td>N/A</td>
                                                }
                                                {
                                                    product.ReferralFee ? (<td>$ {product.ReferralFee}</td>) :
                                                        <td>N/A</td>
                                                }
                                                <td>{product.PostedDate}</td>
                                                <td>{product.SellerSKU}</td>
                                                <td>{product.ASIN}</td>
                                                <td>{product.ProductDesc}</td>
                                            </tr>
                                        })
                                    }


                                </tbody>
                            </Table>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    products: state.product.products
})

export default connect(mapStateToProps, { fetchProducts })(manageProducts);
